body {
  background: $c-gray;

}

html {
  min-width: 320px;
}

.container {
  position: relative;
  max-width: 1150px;
  height: 100%;
  margin: 0 auto;

  &--aproach {
    max-width: 950px;
    background: #ff9c8e;
    padding: 204px;
    border-radius: 200px 0 0 200px;

    &::after {
      content: "";
      position: absolute;
      background: #ff9c8e;
      top: 0;
      bottom: 0;
      right: 0;
      left: 50%;
      z-index: -1;
    }
  }
}
